<template>
  <div>
    <input type="text" v-model="title" />
    <button @click="addFn">添加</button>
    <ul>
      <li v-for="(item, index) in todos" v-bind:key="index">
        {{ item.id }} {{ item.title }}
        <button @click="minusFn(index)">删除</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      todos: [
        { id: 1, title: "吴语婷" },
        { id: 2, title: "方玄" },
        { id: 3, title: "王勇" },
      ],
    };
  },
  created() {},
  mounted() {},
  methods: {
    addFn() {
      if (this.title) {
        this.todos.push({
          id: this.todos.length + 1,
          title: this.title,
        });
      }
    },
    minusFn(index) {
      this.todos.splice(index, 1);
    },
  },
};
</script>

<style scoped>
ul,
ol,
li {
  list-style: none;
}
</style>
